

















































.body{
  font-size: 16px;
  position: relative;
}
.header{
  width: 100%;
  z-index: 1;

}
.cao-top{
  background-color:#F91C88 ;
  position: fixed;
  top: 0px;
  height: 60px;
  width: 100%;
  /*background:#FA298F;*/
  display:-webkit-box;
  display:-ms-flexbox;
  display:flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-weight: bold;
  color: white;
  z-index: 13



}
.cao-top .right{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-right: 18px;

}
.cao-top .left{
  margin-left: 15px;
}

.cao-top .right .out1{
  padding: 0px 5px;
}
.cao-top .right .in1{
  padding: 0px 5px;
  opacity: 0.5;
}
.space1{
  height: 60px;
  width: 100%;
}
.message{
  width: 100%;
  margin-bottom: 50px;
}
.cao-loading-area {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;;
  margin-top: 200px;
}
.classify{
  background: -webkit-linear-gradient(top, #F91C88 ,#FF5FAD );
  background: linear-gradient(to bottom, #F91C88 ,#FF5FAD );
}
.foot{
  width: 100%;
  position: fixed;
  bottom: 0;
  background: #ffffff;
  border-top: solid 1px #cccccc;
  z-index: 1;
}
.text{
  margin: 15px 20px;
  width: 100%;
}
.cao-fail{
  margin-top: 300px ;
}




