










































.cao-detail{
  position: relative;

}
.cao-header{

  color: #ffffff;

}
.header-cao{
  width: 100%;
  background-color: #F91C88;
  position: fixed;
  left:0;
  top: 0;
  z-index: 1;
}
.header-top{
 width: 100%;

  height: 60px;
  -webkit-box-align: center;
      -ms-flex-align: center;
              -ms-grid-row-align: center;
          align-items: center;
  font-size: 16px;

}
.header-label_name{
  width: 100%;
  background: -webkit-linear-gradient(top, #F91C88 ,#FF5FAD );
  background: linear-gradient(to bottom, #F91C88 ,#FF5FAD );
}
.header-bottom{
  margin-top: 59px;
  width: 100%;
  height: 50px;
  margin-left: 20px;
  line-height: 20px;

}
.cao-center{
  margin-bottom: 50px;
}
.cao-foot{
  width: 100%;
  position: fixed;
  bottom: 0;
  height: 50px;
  border-top: solid 1px #cccccc;
  background-color: #ffffff;
  z-index: 1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.foot-message{
  width: 70%;
  margin-left:20px;
  outline: none;
}
input::-webkit-input-placeholder {
  color:#cccccc;
}
input::-moz-placeholder {
  color:#cccccc;
}
.cao-topic-loading-area {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;;
  margin-top: 200px;
}
.cao-topic-fail{
  margin-top: 300px;
}





