









































.answer-topic{
  position: relative;
  width: 100%;
}
.answer-header{
  width: 100%;
  color: #ffffff;

}
.answer-header-name{
  background-color: #0274FF;
  position: fixed;
  top: 0;
  z-index: 1;
  width: 100%;

}
.header-top{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 60px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-left: 10px;
  font-size: 16px;

}
.answer-header-label_name{
  background: -webkit-linear-gradient(top, #0274FF ,#5FA7FF );
  background: linear-gradient(to bottom, #0274FF ,#5FA7FF );
  width: 100%;

}
.header-bottom{

  width: 50%;
  height: 50px;
  margin-left: 20px;
  line-height: 20px;

}
.answer-topic-center{
  width: 100%;
  margin-bottom: 50px;
}

.answer-topic-loading-area {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;;
  margin-top: 200px;
}
.answer-foot{
  width: 100%;
  position: fixed;
  bottom: 0;
  height: 50px;
  border-top: solid 1px #cccccc;
  background-color: #ffffff;
  z-index: 1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.answer-foot-message{
  width: 70%;
  margin-left:20px;
  outline: none;
}
.cao-topic-fail{
  margin-top: 200px;
}




