














.classify-wrapper {
  width: 100%;
  overflow: hidden;
  height: 50px;
  padding: 0 0 10px 0px;
  position: relative;
}

.classify-head {
  width: 100%;
}

.classify-header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  overflow: auto;
  height: 70px;
  margin-top: 5px;

  padding-left: 20px;

}

.hide-scroll::-webkit-scrollbar,
.hide-scroll::-webkit-scrollbar-track,
.hide-scroll::-webkit-scrollbar-thumb {
  width: 0;
  border-width: 0;
  height: 0;
}

.classify-message {
  height: 20px;
  border-radius: 4px;
  padding: 8px;
  margin-right: 10px;
  font-size: 14px;
  background-color: white;
  white-space: nowrap;

}
